os se utiliza el golpe del agua con una superficie para disolver oxígeno presente en la atmósfera y aumentar el valor del parámetro. ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row bg3-tema3 align-items-center mb-5",attrs:{"data-aos":"fade-right"}},[_c('h4',{staticClass:"text-white mt-2"},[_vm._v("Demanda Bioquímica de Oxígeno (DBO)")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('p',[_vm._v("Hace referencia al contenido de materia orgánica que puede ser degradada por los microorganismos presentes en la muestra de agua en un lapso de 5 días con una incubación a 20° C de temperatura, periodo en el cual habrá una variación en el contenido de oxígeno disuelto, por lo que el resultado final se expresa en mgO2/L (Comisión Estatal del Agua de Jalisco (CEA , 2013)). ")]),_c('p',[_vm._v("Cuando la DBO5 no disminuye a pesar de los tratamientos en la PTAP se deben revisar los procesos microbiológicos que esta pueda tener, lodos activados, humedales artificiales, biodiscos o cualquier otro. Inicialmente, se debe revisar que los tiempos de retención se cumplan, en caso de que se cumplan los tiempos, pero la DBO no disminuya. Se debe revisar que los agentes biológicos se encuentren en las concentraciones óptimas para los procesos de degradación de materia orgánica. ")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/img17.svg"),"alt":"Imágen decorativa"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row bg3-tema3 align-items-center mb-5",attrs:{"data-aos":"fade-right"}},[_c('h4',{staticClass:"text-white mt-2"},[_vm._v("Demanda Química de Oxígeno (DQO)")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._se